@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --primary-color: #ff4b2b;
  --gradient-color: #ff416c;
  --secondary-color: #aaa;
  --light-bg: #ffffff;
}

* {
  box-sizing: border-box;
}

/* Main Body Styling */
body {
  font-family: "Montserrat", sans-serif;
  background: #f6f5f7;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: -20px 0 50px;
}

h1 {
  font-weight: bold;
  margin: 0;
}

p {
  font-size: 14px;
  font-weight: 100;
  line-height: 20px;
  letter-spacing: 0.5px;
  margin: 20px 0 30px;
}

span {
  font-size: 12px;
}

a {
  color: #333333;
  font-size: 14px;
  text-decoration: none;
  margin: 15px 0;
}

.container {
  background: #ffffff;
  background: var(--light-bg);
  border-radius: 10px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  position: relative;
  overflow: hidden;
  width: 786px;
  max-width: 100%;
  min-height: 480px;
}

/* FORM CONTAINER STYLING */

.form-container form {
  background: #ffffff;
  background: var(--light-bg);
  display: flex;
  flex-direction: column;
  padding: 0 50px;
  height: 100%;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.social-container {
  margin: 20px 0;
}

.social-container a {
  border: 1px solid #ddd;
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 0 5px;
  height: 40px;
  width: 40px;
}

.form-container input {
  background: #eeeeee;
  border: none;
  padding: 12px 15px;
  margin: 8px 0;
  width: 100%;
}

/* Button styling */
button {
  border-radius: 20px;
  border: 1px solid #ff4b2b;
  border: 1px solid var(--primary-color);
  background: #ff4b2b;
  background: var(--primary-color);
  color: white;
  font-size: 12px;
  font-weight: bold;
  padding: 12px 45px;
  letter-spacing: 1px;
  text-transform: uppercase;
  -webkit-transition: -webkit-transform 80ms ease-in;
  transition: -webkit-transform 80ms ease-in;
  transition: transform 80ms ease-in;
  transition: transform 80ms ease-in, -webkit-transform 80ms ease-in;
  cursor: pointer;
}
button:active {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}
button:focus {
  outline: none;
}
button:hover {
  color: #ff4b2b;
  color: var(--primary-color);
  background-color: white;
}

button.ghost {
  background: transparent;
  border-color: #fff;
}

button.ghost:hover {
  color: white;
}

.form-container {
  position: absolute;
  top: 0;
  height: 100%;
  -webkit-transition: all 0.6s ease-in-out;
  transition: all 0.6s ease-in-out;
}

.login-container {
  left: 0;
  width: 50%;
  z-index: 2;
}

.register-container {
  left: 0;
  width: 50%;
  z-index: 1;
  opacity: 0;
}

.overlay-container {
  position: absolute;
  top: 0;
  height: 100%;
  left: 50%;
  width: 50%;
  overflow: hidden;
  -webkit-transition: -webkit-transform 0.6s ease-in-out;
  transition: -webkit-transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
  z-index: 2;
}
.overlay {
  background: #ff416c;
  background: var(--gradient-color);
  background: -webkit-gradient(
      linear,
      left top, right top,
      from(#ff4b2b),
      to(#ff416c))
    no-repeat 0 0 / cover;
  background: linear-gradient(
      to right,
      #ff4b2b,
      #ff416c)
    no-repeat 0 0 / cover;
  background: -webkit-gradient(
      linear,
      left top, right top,
      from(var(--primary-color)),
      to(var(--gradient-color))
    )
    no-repeat 0 0 / cover;
  background: linear-gradient(
      to right,
      var(--primary-color),
      var(--gradient-color)
    )
    no-repeat 0 0 / cover;
  color: #ffffff;
  color: var(--light-bg);
  position: relative;
  left: -100%;
  height: 100%;
  width: 200%;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  -webkit-transition: -webkit-transform 0.6s ease-in-out;
  transition: -webkit-transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
}

.overlay-panel {
  position: absolute;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 50%;
  padding: 0 40px;
  text-align: center;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  -webkit-transition: -webkit-transform 0.6s ease-in-out;
  transition: -webkit-transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out;
  transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
}
.overlay-register {
  right: 0;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.overlay-login {
  -webkit-transform: translateX(-20%);
          transform: translateX(-20%);
}

/* animation */
/* Move Sign In to right */
.container.register-active .login-container {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
/* move overlay to left */

.container.register-active .overlay-container {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

/* bring signup over sign in */

.container.register-active .register-container {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
  z-index: 5;
  opacity: 1;
}

/* Bring login overlay to left  */

.container.register-active .overlay {
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
}

.container.register-active .overlay-login {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
.container.register-active .overlay-register {
  -webkit-transform: translateX(20%);
          transform: translateX(20%);
}

